.todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  border: 4px solid transparent;
  border-radius: 10px;
  background-color: #6d8299;

  &-status {
    border-color: #f4d19b;

    h3 {
      color: #f4d19b;
    }

    &--completed {
      border-color: #9cc094;
      background-color: #f6f6f6;

      h3 {
        color: #9cc094;
      }
    }
  }

  &__task {
    font-size: 14px;
  }
}
